<template>
  <app-page title="Input " description="">

    <app-case title="">
      <zk-input class="demo-input" placeholder="" />
    </app-case>

    <app-case title="">
      <zk-input class="demo-input" placeholder="" disabled />
    </app-case>

    <app-case title="">
      <zk-input class="demo-input" placeholder="" clearable />
    </app-case>

    <app-case title=" icon ">
      <zk-input class="demo-input" placeholder="" suffix-icon="el-icon-date"></zk-input>
      <zk-input class="demo-input" placeholder="" prefix-icon="el-icon-search"></zk-input>
    </app-case>

    <app-case title="" description=" type  textarea">
      <zk-input class="demo-textarea" type="textarea" :rows="2" placeholder=""></zk-input>
    </app-case>

    <app-case title="">
      <zk-input class="demo-textarea" type="textarea" :autosize="{ minRows: 2, maxRows: 4}" placeholder=""></zk-input>
    </app-case>

    <app-case title="">
      <app-row>
        <zk-input placeholder="">
          <template slot="prepend">Http://</template>
        </zk-input>
      </app-row>
      <app-row>
        <zk-input placeholder="">
          <template slot="append">.com</template>
        </zk-input>
      </app-row>
      <app-row>
        <zk-input placeholder="">
          <zk-select style="width: 120px;" value="1" slot="prepend" placeholder="">
            <zk-option label="" value="1"></zk-option>
            <zk-option label="" value="2"></zk-option>
            <zk-option label="" value="3"></zk-option>
          </zk-select>
          <zk-button slot="append" icon="el-icon-search"></zk-button>
        </zk-input>
      </app-row>
    </app-case>

    <app-case title="">
      <zk-input class="demo-input" placeholder="" suffix-icon="el-icon-date"></zk-input>
      <zk-input class="demo-input" size="medium" placeholder="" suffix-icon="el-icon-date"></zk-input>
      <zk-input class="demo-input" size="small" placeholder="" suffix-icon="el-icon-date"></zk-input>
      <zk-input class="demo-input" size="mini" placeholder="" suffix-icon="el-icon-date"></zk-input>
    </app-case>

    <app-case title="" description="">
      <zk-autocomplete
        class="demo-input"
        placeholder=""
        :fetch-suggestions="(s, c) => c([{value: '', address: '144' }, { value: '', address: '661' }])">
      </zk-autocomplete>
      <zk-autocomplete
        class="demo-input"
        placeholder=""
        :fetch-suggestions="(s, c) => c([{value: '', address: '144' }, { value: '', address: '661' }])">
        <template slot-scope="{ item }">
          <div>{{ item.value }}</div>
          <span style="font-size: 12px;color: #b4b4b4;">{{ item.address }}</span>
        </template>
      </zk-autocomplete>
      <zk-autocomplete
        class="demo-input"
        placeholder=""
        :fetch-suggestions="() => null">
      </zk-autocomplete>
    </app-case>

  </app-page>
</template>

<script>
import Input from '@/packages/input'
import Autocomplete from '@/packages/autocomplete'
import Button from '@/packages/button'
import Select from '@/packages/select'
import Option from '@/packages/option'

export default {
  components: {
    'zk-input': Input,
    'zk-autocomplete': Autocomplete,
    'zk-button': Button,
    'zk-select': Select,
    'zk-option': Option
  }
}
</script>

<style lang="scss" scoped>
.el-row {
  margin-bottom: 20px;
}
.demo-input {
  width: 180px;
  margin: 0 10px 10px 0;
}
.demo-textarea {
  width: 414px;
  margin: 0 10px 10px 0;
}
</style>
